.v-marker-tooltip {
  min-width: 140px;
  max-width: 250px;
  inline-size: max-content;
  background: $tooltip-bg-color;
  color: $tooltip-color;
  position: absolute;
  top: 20%;
  left: 130%;
  padding: $tooltip-arrow-width;
  border: 1px solid #fff;
  box-shadow: 6px 6px 2px rgba(0, 0, 0, 0.25);
  transform-origin: center left;
  transform: scale(0);
  opacity: 0;
  transition: 0.15s ease;
  transition-delay: 0s;
  z-index: $tooltip-z-index;

  &-title {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 120%;
    margin-bottom: 5px;
  }

  &-description {
    font-size: 17px;
    line-height: 1.4;
    font-weight: 400;
  }

  &:before,
  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    background-color: transparent;
    z-index: $tooltip-z-index;
  }

  &:before {
    height: 0;
    width: 0;
    border-color: $tooltip-bg-color transparent transparent transparent;
    border-style: solid;
    border-width: $tooltip-arrow-width - 5px $tooltip-arrow-width
      $tooltip-arrow-width $tooltip-arrow-width + 5px;
    left: -$tooltip-arrow-width;
    box-shadow: 0 -1px 0 #fff;
  }

  &:after {
    height: 100%;
    width: $tooltip-arrow-width * 2;
    left: -$tooltip-arrow-width * 2;
  }

  // tooltip with right positioning
  &.right {
    right: 130%;
    left: auto;
    transform-origin: center right;

    &:before {
      border-color: transparent transparent $tooltip-bg-color transparent;
      transform: rotate(180deg);
      right: -$tooltip-arrow-width;
      left: auto;
      box-shadow: 0 1px 0 #fff;
    }

    &:after {
      width: $tooltip-arrow-width * 2;
      right: -$tooltip-arrow-width * 2;
      left: auto;
    }
  }

  a {
    display: inline-block;
    position: relative;
    padding-bottom: 20px;
    text-decoration: none;
    color: #fff;
    transition: text-shadow 0.2s linear;
    &:after,
    &:before {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      width: 100%;
      max-width: 120px;
      height: 19px;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 96% 100%;
      transform: translateX(-50%);
      transition: bottom 0.1s linear;
    }
    &:after {
      background-image: url(icon('arrow.svg'));
    }
    &:before {
      background-image: url(icon('arrow-secondary.svg'));
    }
    &:hover {
      text-shadow: 0 0 2px $secondary;
      &:after {
        bottom: -6px;
      }
      &:before {
        bottom: -7px;
      }
    }
    &.w-100 {
      display: block;
      text-align: center;
    }
  }
}

&.open {
  .v-marker-tooltip {
    transform: scale(1);
    opacity: 1;
    transition-delay: $tooltip-opening-transition;
  }
}

// change size for specific tooltips
&.epic,
&.cashback,
&.scanner {
  .v-marker-tooltip {
    max-width: 310px;
  }
}
