@import 'vars.scss';
// @import 'dev.scss';
@function icon($fileName) {
  @return 'https://zipzip1312.github.io/Vygoda-City/images/icons/' + $fileName;
  // @return 'http://localhost:3000/images/icons/' + $fileName;
}
// ----------------------------------------------------------------
* {
  box-sizing: border-box;
}
#root {
  min-height: 800px;
  background-color: #fff;
  padding: 0 7.85%;

  @media (max-width: 1280px) {
    padding: 0 5.5%;
  }

  @media (max-width: 1022px) {
    padding: 0 10px;
  }
}
img {
  width: 100%;
  max-width: 100%;
}
// ----------------------------------------------------------------

.v-city {
  &-mobile {
    display: none;
  }

  .v-map-wrapper {
    position: relative;
  }

  .v-areas {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: $areas-z-index;

    .v-area {
      position: absolute;
      transform: skew(60deg, -30deg);
      cursor: default;

      @import 'areas.scss';
    }
  }

  .v-markers {
    .v-marker {
      position: absolute;
      width: $marker-width;
      padding-bottom: 2%;
      transition: top ease $tooltip-opening-transition;
      cursor: default;
      z-index: $marker-z-index;
      &.open {
        padding-bottom: 5%;
      }

      @import 'tooltips.scss';
      @import 'markers.scss';
    }
  }

  .v-active-areas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    .v-active-area {
      position: absolute;
      display: none;
      &.open {
        display: block;
      }
      @import 'active-areas.scss';
    }
  }

  @import 'elements.scss';
}

@media (max-width: $mobile-breakpoint) {
  .v-city {
    display: none;
  }
  .v-city-mobile {
    display: block;
    @import 'mobile.scss';

    .cashback-info {
      padding-top: 0;
      .icon {
        width: 30px;
        height: 30px;
      }
    }
  }
}

@import 'info-tabs.scss';

// ----------------------------------------------------------------
// Common
// ----------------------------------------------------------------
.app-download-links {
  display: flex;
  > a:first-child {
    margin-right: 15px;
  }
  a {
    padding-bottom: 0 !important;
    &:after,
    &:before {
      display: none;
    }
  }
}

.cashback-info {
  display: flex;
  margin-bottom: 7px;
  padding-top: 7px;
  align-items: center;
  .icon {
    width: 40px;
    height: 40px;
    margin-right: 10px;
  }
  .icon-description {
    display: flex;
    flex-direction: column;
  }
}
