@mixin activate-tab() {
  .v-city-info-tab__header {
    &:after,
    &:before {
      bottom: 7px;
    }
    &:after {
      opacity: 0;
    }
  }
}

.v-city-info {
  display: flex;
  justify-content: space-between;
  margin-top: 60px;
  padding-bottom: 60px;
  &-tab {
    width: 100%;
    max-width: 250px;
    position: relative;
    background-color: #fff;
    color: #333;
    &:not(:last-child) {
      margin-right: 10%;
    }
    &__header {
      width: 100%;
      padding: 20px 0 25px 20px;
      background-color: $primary;
      position: relative;
      cursor: pointer;
      > img {
        position: absolute;
        height: 80px;
        width: auto;
        right: -28%;
        bottom: -19%;
      }
      &:after,
      &:before {
        content: '';
        position: absolute;
        left: 20px;
        bottom: 10px;
        width: 60px;
        height: 13px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100%;
        transition: opacity 0.1s linear, bottom 0.1s linear;
      }
      &:after {
        background-image: url(icon('arrow-dark.svg'));
        opacity: 1;
      }
      &:before {
        background-image: url(icon('arrow.svg'));
      }
    }
    &:hover {
      @include activate-tab;
    }
    &__content {
      height: 0;
      overflow: hidden;
      transition: height 0.15s linear;
      .v-info-how-to {
        margin-top: 5px;
        &-item {
          display: flex;
          align-items: center;
          padding-left: 5px;
          &__icon {
            width: 80px;
            height: 80px;
            display: flex;
            justify-content: center;
            img {
              max-width: 80px;
              max-height: 80px;
              margin-bottom: -5px;
            }
          }
          &__description {
            flex-grow: 1;
            font-size: 14px;
            margin-left: 10px;
            line-height: 1.4;
          }
        }
      }
      .v-faq-list {
        &-item {
          &__title {
            position: relative;
            margin: 5px auto;
            padding: 10px 25px 10px 0;
            border-bottom: 1px solid $primary;
            cursor: pointer;
            outline: none;
            &::marker,
            &::-webkit-details-marker {
              content: '';
              display: none;
            }
            &::before {
              content: '';
              position: absolute;
              right: 0;
              top: 50%;
              width: 20px;
              height: 10px;
              background: url(icon('arrow-secondary.svg')) no-repeat center;
              transform: translateY(-50%);
              transition: transform 0.2s linear;
            }
          }
          &__description {
            background-color: $primary;
            padding: 5px;
            margin-top: -5px;
            line-height: 1.3;
          }
        }
        [open] {
          .v-faq-list-item__title::before {
            transform: rotate(-180deg);
            top: 40%;
          }
        }
      }
    }
    &.open {
      .v-city-info-tab__content {
        height: 100%;
      }
      @include activate-tab;
    }
  }
}

@media (max-width: $mobile-breakpoint) {
  .v-city-info {
    flex-direction: column;
    margin-top: 20px;
    overflow: hidden;
    &-tab {
      max-width: 100%;
      margin-right: 0 !important;
      margin-bottom: 20px;
      &__header {
        width: 100%;
        > img {
          right: -22px;
        }
      }
    }
  }
}

@media (min-width: 961px) and (max-width: 1280px) {
  .v-city-info {
    &-tab {
      max-width: 230px;
      margin-right: 0 !important;
      &__header {
        padding: 15px 0 25px 15px;
        &:after,
        &:before {
          left: 20px;
        }
        > img {
          height: 60px;
          right: -18%;
          bottom: -14%;
        }
      }
    }
  }
}
