body {
  margin: 0;
  font-family: roboto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: #fff;
  margin: 0 auto;
  max-width: 1920px;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  width: 100%;
  max-width: 1214px;
  margin: 25px auto 50px;
}
.header > img,
.footer > img {
  width: 100%;
  max-width: 100%;
}
.layout-sm,
.layout-xs {
  display: none;
}

@media (max-width: 1280px) {
  .wrapper {
    max-width: 873px;
  }
  .layout-lg {
    display: none;
  }
  .layout-sm {
    display: block;
  }
}

@media (max-width: 960px) {
  .wrapper {
    max-width: 100%;
  }
  .layout-sm {
    display: none;
  }
  .layout-xs {
    display: block;
  }
}
