.v-elements {
  > div {
    position: absolute;
  }
  .super-cashback {
    width: 14.5%;
    top: 62.6%;
    left: 33.5%;
    &.open {
      transform: scaleX(-1);
    }
  }
  .visa {
    width: 10.1%;
    height: 19%;
    top: 56.8%;
    left: 46.6%;
    overflow: hidden;
    transition: top 0.3s ease;
    &:after {
      position: absolute;
      content: '';
      width: 80%;
      height: 54%;
      top: 59%;
      left: -50%;
      transform: skew(0deg, 31deg);
      background: linear-gradient(
        to right,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 0.8) 30%,
        rgba(128, 186, 232, 0) 50%,
        rgba(125, 185, 232, 0) 100%
      );
    }
    &.open {
      &:after {
        animation: shine-and-slide 1.2s ease;
      }
    }
  }
  .cashback {
    width: 6.4%;
    top: 62.6%;
    left: 67.5%;
    &.open {
      animation: bounce 1.1s linear;
      transform-origin: center bottom;
    }
  }
  .online {
    &.serfer {
      width: 12%;
      top: 58%;
      left: 80.2%;
    }
    &.cart {
      width: 9%;
      top: 51.7%;
      left: 77.2%;
    }
    &.open {
      &.serfer {
        animation: surf 0.7s linear none 2 alternate;
      }
      &.cart {
        animation: cart 0.4s linear 0.2s 2 alternate;
      }
    }
  }
  .tv {
    width: 11%;
    top: 79%;
    left: 26%;
    &-link {
      display: none;
      position: absolute;
      top: 18%;
      left: 12.6%;
      width: 84%;
      height: 38%;
      background: #a4c8f4;
      color: #fff;
      transform: skew(2deg, -30deg);
      background: $primary;
      &-text {
        margin: auto;
        text-align: center;
        font-size: 13px;
      }
    }
  }
}

@keyframes shine-and-slide {
  0% {
    opacity: 0.8;
    top: -14%;
    left: -84%;
  }
  40% {
    top: 59%;
    left: 100%;
    opacity: 0.8;
  }
  60% {
    opacity: 0.5;
  }
  100% {
    left: 150%;
    opacity: 0;
  }
}

@keyframes bounce {
  from,
  20%,
  53%,
  to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translate3d(0, 0, 0);
  }

  40%,
  43% {
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    transform: translate3d(0, -12%, 0);
  }

  70% {
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    transform: translate3d(0, -5%, 0);
  }

  80% {
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translate3d(0, 0, 0);
  }

  90% {
    transform: translate3d(0, -2%, 0);
  }
}

@keyframes surf {
  from {
    top: 58%;
    left: 80.2%;
    transform: rotate(0deg);
  }
  20% {
    top: 59%;
    left: 79.2%;
    transform: rotate(3deg);
  }
  50% {
    top: 57.5%;
    left: 80%;
    transform: rotate(7deg);
  }
  70% {
    top: 57%;
    left: 81%;
    transform: rotate(-10deg);
  }
  to {
    top: 56.7%;
    left: 81.6%;
    transform: rotate(0deg);
  }
}

@keyframes cart {
  from {
    top: 51.7%;
    left: 77.2%;
  }
  to {
    top: 53.7%;
    left: 75.2%;
  }
}
