@mixin marker($top, $left) {
  top: $top * 1%;
  left: $left * 1%;
  &.open {
    top: ($top - $marker-lift) * 1%;
    z-index: $tooltip-opened-z-index;
  }
}

&.offers {
  @include marker(33, 22);
}
