.v-group {
  padding: 20px 0;
  background-color: #fff;
  color: #fff;
  font-size: 14px;
  &-header {
    position: relative;
    &-title {
      position: absolute;
      top: 5%;
      right: 3%;
      font-size: 4vw;
      display: none;
    }
  }
  &-info-wrapper {
    display: flex;
    flex-wrap: wrap;
    background-color: $primary;
    padding: 15px 15px 5px;
    .v-group-info {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      color: $secondary;
      width: 50%;
      margin-bottom: 10px;
      &:nth-child(2n) {
        text-indent: 3vw;
      }
      &-marker {
        width: 14%;
      }
      &-title {
        text-transform: uppercase;
        font-size: 4vw;
        margin-left: 10px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          right: -5vw;
          top: 1vw;
          width: 5vw;
          height: 2vw;
          background-repeat: no-repeat;
          background-position: center;
          background-size: 100%;
          transform: rotate(-90deg);
          background-image: url(icon('arrow-secondary.svg'));
        }
      }
    }
  }
}

.v-modal-wrapper {
  position: fixed;
  z-index: 9999999;
  // padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  overflow: auto;
  display: flex;

  .v-modal {
    width: 100%;
    max-width: 320px;
    min-width: 300px;
    position: relative;
    padding: 20px 20px 20px 30px;
    margin: auto;
    background-color: #fefefe;
    color: $secondary;
    box-shadow: 0 2px 15px rgba(0, 0, 0, 0.6);
    animation: modalPopUp 0.3s linear;

    &-close {
      position: absolute;
      top: 0;
      right: 0;
      padding: 5px 13px;
      font-size: 25px;
      color: $secondary;
      cursor: pointer;
    }

    &-header {
      display: flex;
      align-items: flex-start;
      margin-left: -10px;

      &-marker {
        margin-right: 20px;
        img {
          max-width: 50px;
          max-height: 80px;
        }
      }

      &-title {
        flex-grow: 1;
        align-self: flex-end;
        font-size: 18px;
        text-transform: uppercase;
        padding-right: 20px;
        line-height: 1.5;
      }
    }

    &-body {
      margin-top: 20px;
      a {
        display: inline-block;
        max-width: 80%;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 5px;
          right: -29px;
          width: 24px;
          height: 12px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: 100%;
          transform: rotate(-90deg);
          background-image: url(icon('arrow-secondary.svg'));
        }
      }
    }
  }
}

@keyframes modalPopUp {
  from {
    opacity: 0;
    transform: translate3d(0, 20%, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
